<template>
	<account activeTab="contacts">
		<section>
			<div class="field_title pt-1 d-flex align-items-center">
				<BIcon icon="code" class="mr-2" scale="1.4" />
				<div class="title font-weight-500">{{ $t("connections") }}</div>
			</div>
			<div class="mt-4">
				<div class="mb-4" v-for="(provider, i) of providers" :key="i">
					<div class="d-flex align-items-center">
						<b-img :src="require(`../../assets/media/${provider.image}`)" width="50px"></b-img>
						<div class="ml-3">
							<div class="mb-1 text-muted">{{ provider.name }}</div>
							<div>{{ provider.email }}</div>
						</div>
						<div class="ml-auto">
							<span class="btn_icon" v-b-tooltip.hover :title="$t('disconnect')" v-if="provider.hasDisconnect">
								<b-iconstack font-scale="0.8">
									<b-icon stacked icon="triangle" shift-v="5"></b-icon>
									<b-icon stacked icon="dash-lg" shift-v="-6"></b-icon>
								</b-iconstack>
							</span>
						</div>
					</div>
				</div>
			</div>
			<button class="btn_outline d-flex align-items-center fs-sm" v-b-modal.ContactsConnectionModal>
				<BIcon icon="plus-lg" class="mr-2" />
				<span>{{ $t("addContactsConnection") }}</span>
			</button>
		</section>
		<ContactsConnectionModal />
	</account>
</template>

<script>
	import Account from "@/components/Account";

	import ContactsConnectionModal from "@/components/modals/ContactsConnectionModal";
	export default {
		components: { Account, ContactsConnectionModal },

		data() {
			return {
				contactImages: {
					"microsoft.com": "microsoft.png",
					"google.com": "google_contacts.png",
				},
				contactNames: {
					"microsoft.com": this.$t("outlookContacts"),
					"google.com": this.$t("googleContacts"),
				},
			};
		},

		computed: {
			zoomEmail() {
				let zoom = this.$me.integrations.find((integration) => integration.providerName === "zoom");

				return zoom ? zoom.userEmail : "";
			},

			isGoogleConnected() {
				let google = this.$me.integrations.find((integration) => integration.providerName === "google.com");

				return google ? true : false;
			},

			googleHasWriteCalendar() {
				let google = this.$me.integrations.find((integration) => integration.providerName === "google.com");

				return google ? google.calendars.some((cal) => cal.shouldWrite) : false;
			},

			providers() {
				let providers = [];

				for (let integration of this.$me.integrations) {
					if (!integration.scopes.includes("contact")) continue;

					providers.push({
						name: this.contactNames[integration.providerName],
						image: this.contactImages[integration.providerName],
						email: integration.userEmail,
						userId: integration.userId,
						hasDisconnect: !this.$me.authProviders.some((p) => p.userId === integration.userId),
					});
				}

				return providers;
			},
		},
	};
</script>

<style lang="scss"></style>
